h5 {
   margin: 20px 0 8px 10px;
   font-size: 18px;
}
li {
   list-style: none;
   padding: 0;
}
a {
   text-decoration: none;
}

a:visited.active {
   color: #ea0029;
}

a:visited {
   color: black;
}

.homeLogo {
   width: 110px;
   height: 100px;
   display: block;
   margin: auto;
}

.navHome {
   width: 100%;
   height: 40px;
   display: block;
   margin: auto;
}

.navHome ul {
   border-bottom: 1px solid #56565640;
}

.navHome ul > li {
   width: 100px;
   height: 30px;
}
.navHome ul {
   height: 100%;
   display: flex;
   justify-content: space-around;
   align-items: center;
   padding-left: 48px;
}

.navHome a {
   color: #565656;
   font-weight: 800;
}

.navHome a.active {
   color: #ea0029;
}

.active .dropdown-content {
   display: block;
}

.dropdown-content {
   min-width: 160px;
   z-index: 1;
}

.dropdown-content ul {
   padding-left: 0;
   display: flex;
   flex-direction: row;
   justify-content: space-around;
   flex-wrap: wrap;
}
.dropdown-content > ul > li {
   font-size: 16px;
}
.dropdown-content > li:last-child {
   padding-bottom: 18px;
}
.dropdown-content > a {
   text-align: center;
   margin: auto;
   float: left;
}
.dropdown-content a:hover {
   color: #ea0029;
}

.tab.active .dropdown-content {
   display: block;
}

#container {
   width: 1500px;
   display: block;
   margin: auto;
}

#contents {
   display: block;
   width: 1260px;
   margin: auto;
}
#contents ul {
   box-sizing: border-box;
   float: left;
   margin: auto;
   margin-top: 70px;
   width: 1260px;
   flex-wrap: wrap;
}
#contents li {
   width: 412px;
   height: 276px;
   float: left;
   margin-bottom: 10px;
}
#contents img {
   width: 412px;
   height: 276px;
   float: left;
}

