.championsField {
   position: relative;
   width: 1200px;
   height: 1500px;
}

.championsField img {
   margin-top: 60px;
}

.championsField > ul {
   width: 1200px;
   flex-wrap: wrap;
}

.championsField > p {
   font-size: 13px;
   margin: auto;
}

.text {
   position: absolute;
   top: 60px;
   left: 50px;
   width: 432px;
   margin-top: 60px;
}

.text h1 {
   display: inline-block;
   font-weight: 600;
   color: white;
   margin-bottom: 10px;
}
.text h1:nth-child(2) {
   margin-left: 10px;
   color: #ea0029;
}

.text p {
   display: block;
   width: 380px;
   text-align: left;
   margin-left: 0;
   color: white;
   margin: 0;
}

.championsFieldTable {
   width: 450px;
   position: absolute;
   top: 195px;
   left: 50px;
   margin-top: 60px;
}
.line {
   width: 432px;
   height: 3px;
   background-color: #ea0029;
   margin-bottom: 15px;
}

.option {
   display: inline-block;
   width: 70px;
   color: white;
   font-size: 15px;
}
.value {
   display: inline-block;
   font-size: 15px;
   margin-left: 7px;
   color: white;
   width: 372px;
}

.value::before {
   margin-right: 15px;
   content: "|";
   color: white;
}

.fa-location-dot {
   position: relative;
   margin-right: 10px;
   color: #ea0029;
}

.directions {
   box-sizing: border-box;
   display: inline-block;
   width: 580px;
   float: left;
   background-color: rgb(248, 248, 250);
   border-radius: 10px;
}

.directions:nth-child(1),
.directions:nth-child(2) {
   height: 202px;
   margin-bottom: 20px;
   padding: 15px;
}
.directions:nth-child(odd) {
   margin-right: 30px;
   padding: 20px;
}
.directions:nth-child(even) {
   margin-right: 0;
   padding: 20px;
}

.bus {
   font-size: 14px;
   position: relative;
   box-sizing: border-box;
   padding-left: 72px;
   width: 530px;
   margin-bottom: 15px;
   word-spacing: -1px;
}
.bus.bus1::before {
   position: absolute;
   content: "버스";
   left: 0;
   top: 0;
   display: inline-block;
   width: 65px;
   height: 25px;
   line-height: 25px;
   color: #fff;
   font-size: 15px;
   background: #05141f;
   border-radius: 20px;
   text-align: center;
   margin-right: 3px;
}

.taxi {
   font-size: 14px;
   position: relative;
   box-sizing: border-box;
   padding-left: 72px;
   width: 530px;
   margin-bottom: 15px;
   word-spacing: -1px;
}
.taxi::before {
   position: absolute;
   content: "택시";
   left: 0;
   top: -4px;
   display: inline-block;
   width: 65px;
   height: 25px;
   line-height: 25px;
   color: white;
   font-size: 15px;
   background: rgb(247, 147, 30);
   border-radius: 20px;
   text-align: center;
   margin-right: 3px;
}
.subway {
   font-size: 14px;
   position: relative;
   box-sizing: border-box;
   padding-left: 72px;
   width: 530px;
   margin-bottom: 15px;
   word-spacing: -1px;
}
.subway.subway1::before {
   position: absolute;
