/* Player */
.coachList {
   box-sizing: border-box;
   width: 1200px;
   display: block;
   margin: auto;
   padding-left: 50px;
   margin-top: 60px;
}

.coachingStaff {
   position: relative;
   width: 210px;
   height: 220px;
   box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
   float: left;
   margin-right: 15px;
   margin-bottom: 20px;
   border-top-right-radius: 8px;
   border-top-left-radius: 8px;
}

.coachingStaff img {
   width: 210px;
}

.staff {
   position: relative;
   top: -63px;
   display: flex;
   height: 100px;
   flex-direction: column;
}

.staff ul {
   height: 100px;
   margin: 0;
}
.staff ul li:first-child {
   position: relative;
   width: 100%;
   left: 6px;
   top: 8px;
   font-size: 17px;
   margin-bottom: 0;
   box-sizing: border-box;
   padding: 0;
   padding-left: 10px;
}

.staff ul li {
   margin: 0;
   padding: 0;
}
.staff li:nth-child(2) span {
   position: absolute;
   left: 8px;
   top: 26px;
   box-sizing: border-box;
   font-size: 24px;
   color: #ea0029;
   padding: 0;
   padding-top: 0px;
   padding-left: 8px;
   padding-bottom: 5px;
}
.staff li:last-child {
   box-sizing: border-box;
   position: absolute;
   top: 60px;
   width: 210px;
   height: 50px;
   font-size: 19px;
   font-weight: 600;
   color: white;
   background-color: #000000;
   opacity: 0.8;
   border-bottom-right-radius: 8px;
   border-bottom-left-radius: 8px;
   padding: 0;
   padding-left: 10px;
   padding-bottom: 10px;
}

.staff li:last-child span {
   position: relative;
   left: 5px;
   top: 10px;
}

